*, :before, :after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

body {
  height: 100%;
  width: 100vw;
}

img {
  width: 100%;
  height: auto;
}

input, button, textarea, select {
  font: inherit;
}

button {
  border: 0 solid #000;
}

ul {
  list-style: none;
}

a, a:link, a:visited, a:active {
  text-decoration: none;
}

h1 {
  color: #000;
  letter-spacing: -.3125rem;
  color: #eeeff4;
  font-family: League Spartan, sans-serif;
  font-size: 15.625rem;
  font-weight: 700;
  line-height: 12.5rem;
}

h2 {
  color: #000;
  letter-spacing: -.075rem;
  font-family: League Spartan, sans-serif;
  font-size: 3rem;
  font-weight: 700;
  line-height: 3rem;
}

@media (min-width: 48rem) {
  h2 {
    letter-spacing: -.125rem;
    font-size: 6rem;
    line-height: 5rem;
  }
}

h3 {
  color: #000;
  letter-spacing: -.106875rem;
  font-family: League Spartan, sans-serif;
  font-size: 3rem;
  font-weight: 700;
  line-height: 3.25rem;
}

@media (min-width: 48rem) {
  h3 {
    letter-spacing: -.125rem;
    font-size: 4.5rem;
    line-height: 4rem;
  }
}

h4 {
  color: #000;
  letter-spacing: 0;
  font-family: League Spartan, sans-serif;
  font-size: 2rem;
  font-weight: 700;
  line-height: 2.5rem;
}

body {
  color: #000;
  letter-spacing: 0;
  color: #60636d;
  font-family: League Spartan, sans-serif;
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.5rem;
}

a, a:visited, a:active {
  color: #1b1d23;
}

.header {
  width: 100%;
  z-index: 2;
  position: fixed;
  top: 0;
}

@media (min-width: 48rem) {
  .header {
    justify-content: center;
    margin: 0;
    display: flex;
  }
}

.header__container {
  width: 100vw;
  height: 100vh;
  grid-template-rows: 2rem 2rem 2rem 14.6875rem 1fr;
  grid-template-columns: 2rem 1fr 2rem;
  display: grid;
}

@media (min-width: 48rem) {
  .header__container {
    height: 9.5rem;
    grid-template-rows: 3.5rem 2.5rem 3.5rem;
    grid-template-columns: 6rem 6rem .1fr 1fr 6rem;
  }
}

@media (min-width: 90rem) {
  .header__container {
    max-width: 90rem;
    grid-template-columns: 10.3125rem 6rem .1fr 1fr 10.3125rem;
  }
}

.header__name {
  display: none;
}

@media (min-width: 48rem) {
  .header__name {
    writing-mode: vertical-rl;
    color: #c8ccd8;
    letter-spacing: 1.125rem;
    align-items: center;
    gap: 3rem;
    font-weight: 500;
    display: flex;
    position: fixed;
    top: 0;
    left: 2.375rem;
  }

  .header__name div {
    height: 6.5rem;
    width: .0625rem;
    background-color: #c8ccd8;
  }
}

@media (min-width: 90rem) {
  .header__name {
    left: 3.9375rem;
  }
}

.header__bar {
  background-color: #fff;
  grid-area: 1 / 1 / 4 / 4;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  line-height: 0;
  display: flex;
}

@media (min-width: 48rem) {
  .header__bar {
    grid-area: 2 / 2 / 3 / 3;
    margin: 0;
    padding: 0;
  }
}

.header__logo {
  max-width: 4.8125rem;
}

@media (min-width: 48rem) {
  .header__burger {
    display: none;
  }
}

.nav {
  color: #000;
  letter-spacing: 0;
  grid-area: 4 / 2 / 5 / 4;
  font-family: League Spartan, sans-serif;
  font-size: 2rem;
  font-weight: 700;
  line-height: 2.5rem;
  transition: all .5s ease-in-out;
  transform: translateX(100vw);
}

@media (min-width: 48rem) {
  .nav {
    color: #000;
    letter-spacing: 0;
    z-index: 1;
    grid-area: 2 / 4 / 3 / 5;
    align-self: center;
    font-family: League Spartan, sans-serif;
    font-size: 1.125rem;
    font-weight: 500;
    line-height: 1.5rem;
    top: 0;
    transform: translateX(0);
  }
}

.nav--active {
  transition: all .5s ease-in-out;
  transform: translateX(0);
}

.nav__menu {
  background-color: #eeeff4;
  flex-direction: column;
  gap: 1.0625rem;
  padding: 2.4375rem 0 2.625rem 3rem;
  display: flex;
}

@media (min-width: 48rem) {
  .nav__menu {
    z-index: 10;
    transition: all none none;
    background-color: #fff;
    flex-direction: row;
    align-items: center;
    gap: 3.4375rem;
    padding: 0;
    display: flex;
  }

  .nav__item a {
    color: #7d828f;
  }
}

.nav__back {
  visibility: hidden;
  z-index: -1;
  opacity: 0;
  background-color: #000;
  grid-area: 4 / 1 / 6 / 4;
  transition: all .5s ease-in-out;
}

@media (min-width: 48rem) {
  .nav__back {
    opacity: 1;
    visibility: visible;
    background-color: #fff;
    grid-area: 1 / 1 / 4 / 6;
  }
}

.nav__back--active {
  opacity: .5;
  visibility: visible;
  transition: all .5s ease-in-out;
}

@media (min-width: 48rem) {
  .nav__back--active {
    opacity: 1;
  }
}

